<template>
    <div id="ifDirective">
        <h2>Contador de Likes</h2>
        <p id="likeslikes">{{ likes }}</p>
        <div v-if="likes === 0"><p>No tienes likes</p></div>
        <div v-else-if="likes < 10"><p>Vas a llegar a 10 likes</p></div>
        <div v-else-if="likes < 20"><p>Vas a llegar a 20 likes</p></div>
        <div v-else><p>Tienes muchos likes</p></div>

        <button v-on:click="addLike">¡Like!</button>
        <button v-on:click="removeLike">¡Dislike!</button>

        <h1>Mis conocimientos de programación son:</h1>
        <div class="container_skill">
            <ul>
                <li v-for="skill in skills" :key="skill">{{ skill }}</li>
            </ul>
            <input type="text" v-model="skill">
            <button @click="addSkill" :disabled="skill==''">Agregar Skill</button>
        </div>
    </div>
</template>

<script>
export default {
    name:"If-component",
    data: function(){
        return {
            skills: ['HTML', 'CSS', 'JS'],
            skill: '',
            likes:0,
        }
    },
    methods:{
        addLike() {
            this.likes++;
        },
        removeLike() {
            if(this.likes> 0){
                this.likes--;
            }
        },
        addSkill() {
            // agregar skill
            this.skills.push(this.skill)
            // limpiar input
            this.skill = ""
        }
    }
}

</script>

<style scoped>
#likeslikes {
    display:inline-block;
    background-color: crimson;
    border-radius: 20px;
    box-shadow: 2px 2px 2px #999;
    padding: 20px 30px;
    color: #fff;
    font-size: 30px;
    font-weight: bold;
    text-shadow: 1px 1px 2px #000;
}
button {
    margin: 0px 10px;
}
</style>