<template>
    <div id="estoEsUnId">
        <h1>Two Way Data Binding</h1>
        <p id="papapaparrafo">Tiene sentido en componentes de formulario</p>
        <input type="text" v-model="nombre" id="input">
        <h2 id="titleTwo">{{ dice }} {{ nombre }}</h2>
        <button v-on:click="cambiarNombre">Cambiar</button>
    </div>
</template>

<script>
export default{
    name: 'two-way-component',
    data: function() {
        return{
            nombre: '',
            dice: 'El input dice: '
        }
    },
    methods: {
        cambiarNombre: function() {
            this.nombre = '¡Cambie el input con el botón!'
        }
    },
}
</script>

<style scoped>
#estoEsUnId {
    background-color: lightsalmon;
    margin: 1% 10%;
    padding: 1%;
}

#papapaparrafo {
    font-size: 9px;
    font-weight: bold;
    letter-spacing: 5px;
    text-transform: uppercase;
}

#input {
    padding: 5px;
    border-radius: 15px;
    width: 400px;
    text-align: center;
    border: 1px solid crimson;
}

#titleTwo {
    background-color: #fff;
    padding: 5px;
}
</style>