<template>
  <div id="app">
    <img v-bind:src="rutaImagenLocal" v-bind:alt="nombreImagen" id="imagenUno">
    <HelloWorld msg="¡Yo lo edite!"/>
    <hr>
    <p id="parrafo1">{{ info }}</p>
    <p id="parrafo2">{{ infoDos }}</p>
    <input type="text" v-bind:value="mensaje" id="cajaTextoUno">
    <br><br>
    <img v-bind:src="rutaImagenWeb" v-bind:alt="nombreImagenDos" id="imagenDos">
    <TwoWay />
    <IfDirective />
  </div>
</template>

<script>

import IfDirective from './components/IfDirective.vue'
import TwoWay from './components/TwoWay.vue'
import HelloWorld from './components/HelloWorld.vue'

export default {
  name: 'App',
  components: {
    HelloWorld,
    TwoWay,
    IfDirective
},
  data: function() {
    return {
      info: `One Way Data Binding (Usando mustaches)`,
      infoDos:`One Way Data Binding: Es el envio de información desde el modelo de datos al template`,
      mensaje: `Usando directiva v-bind`,
      rutaImagenLocal: require('@/assets/img/bomba.jpg'),
      rutaImagenWeb: 'https://api.ambr.top/assets/UI/namecard/UI_NameCardPic_Klee_P.png',
      nombreImagen: 'Bomba Klee',
      nombreImagenDos: 'Tarjeta Klee'
    }
  },
  methods: {}
}


// Cualquier imagen traída directamente desde la web se puede alojar en una propiedad del modelo de datos
//rutaImagen: '@/assets/img/bomba.jpg',
// Cuando se coloca la ruta de la imagen directa, el html sabe que es un recurso, pero cuando se hace One Way Data Binding la imagen se tiene que tratar como un modulo para que se pueda trabajar, así que hay que usar la función require para que funcione tanto localmente como al subir al servidor
</script>

